import NavBar from '../components/NavBar/NavBar';
import PostGrid from '../components/Posts/PostGrid';
import {makeCMSContentRequest} from '../services/CMSrequest';
import React from 'react';
import styled from '@emotion/styled';
import { url } from 'inspector';

const Index = (data: any) => {
  return(
    <>
      <NavBar/>
        <HomePageContainer>
          <PostGrid posts = {data}/>
        </HomePageContainer>
    </>
  )
}

Index.getInitialProps = async() => {
  const data = await makeCMSContentRequest('blogPost');
  return {data};
}

const HomePageContainer=styled.div`
  background-image: url("https://www.google.com/url?sa=i&source=images&cd=&ved=2ahUKEwjp14T92NLiAhVkRN8KHbZODNIQjRx6BAgBEAU&url=https%3A%2F%2Fwww.textures.com%2Fdownload%2Fwoodplanksclean0025%2F4612&psig=AOvVaw2MxhYLa9Yubj8EvcgOsOlb&ust=1559836222198903");
`

export default Index
